.l-mv{
width: 100%;
padding-top:rem(130);
// height: 100vh;
min-height: 100vh; /* カスタムプロパティ未対応ブラウザ用のフォールバック */
min-height: calc(var(--vh, 1vh) * 100);
padding-left: rem(40);
padding-right: rem(40);
@include mq(sp){
  padding-left: rem(15);
  padding-right: rem(15);
  padding-top: rem(80);
}
}
.p-mv{
   
}

.mv-container{
    max-width: rem(1200);
    position: relative;
    height: 100%;
  //  height: calc(100vh - 180px );
   height: calc(var(--vh, 1vh) * 100 - 180px);
   width: 100%;
   margin: 0 auto;
   @include mq(sp){
  height: calc(var(--vh, 1vh) * 100 - 130px);
   }
}
.mv__wrapper{
    height: inherit;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.swiper-wrapper{
height: 100%;
// height: calc(100vh - 179px );
height: calc(var(--vh, 1vh) * 100 - 179px);
width: 100%;
@include mq(sp){
height: calc(var(--vh, 1vh) * 100 - 130px);

}
}
.swiper-slide{
height: 100%;
position: relative;
padding-top: 56.25% ;
// padding: 10px;

}
@media screen and (min-width: 1000px) and (max-width: 1175px){
  .swiper-slide{
    padding-top: 64.25%;
  }

}
.slide-img{
 position: absolute;
  top: 0;
  left: 0;
   width: 100%;
   height: 100%;
  //  height: calc(100vh - 11.18rem );
   height: calc(var(--vh, 1vh) * 100 - 11.18rem);
   @include mq(sp){
    height: calc(var(--vh, 1vh) * 100 - 8.125rem);

  }
  }
.slide-img-1{
   display: block;
   background: url(../../assets/img/mv-pc-1.jpg)no-repeat top center / cover;
   @include mq(sp){
    background: url(../../assets/img/mv-sp-15.jpg)no-repeat top center / cover;
  }
  }
.slide-img-2{
  display: block;
   background: url(../../assets/img/mv-pc-2.jpg)no-repeat top center / cover;
   @include mq(sp){
    background: url(../../assets/img/mv-sp-6.jpg)no-repeat top center / cover;
  }
   @include mq(ssp){
    background: url(../../assets/img/mv-sp-6.jpg)no-repeat  top right / cover;
  }
  }
.slide-img-3{
  display: block;
   background: url(../../assets/img/mv-pc-3.jpg)no-repeat top center / cover;
   @include mq(sp){
    background: url(../../assets/img/mv-sp-10.jpg)no-repeat top center / cover;
  }
  }
  
.p-mv_title-1st,.p-mv_title-2nd,.p-mv_title-3rd{
position: absolute;
top: 0%;
left: 50%;
transform: translateX(-50%);
color: $text-color;
font-size:rem(40);
z-index: 2;
width: 100%;
background: $white;
text-align: center;
padding-top: rem(30);
padding-bottom: rem(30);
padding-left: 15px;
padding-right: 15px;
line-height: 1.25;
// font-weight: normal;
@include mq(tab){
  font-size: 35px;
  padding-top: 15px;
  padding-bottom: 20px;
}

@include mq(sp){
  font-size: 25px;
}
}
.p-mv_title-1st{
  border: solid 3px $text-color;
}
.p-mv_title-2nd{
  border: solid 3px $text-color;

}
.p-mv_title-3rd{
  border: solid 3px $text-color;

}
.p-mv_title-1st span,.p-mv_title-2nd span,.p-mv_title-3rd span{
  
  display: inline-block;
  position: relative;
  padding: 7px 15px;
  font-size:rem(18);/*フォントサイズ*/
  color: #FFF;/*フォントカラー*/
  background: $base-color;/*背景色*/
  font-weight: normal;
  @include mq(tab){
    font-size: 18px;
   }
  @include mq(sp){
    font-size: 16px;
    padding: 7.5px 10px;
    margin-top: rem(10);
  }
}
.p-mv_title-1st span{
  background: $base-color;/*背景色*/
 
}
.p-mv_title-2nd span{
  background: #F4BE7F;/*背景色*/
}
.p-mv_title-3rd span{
  background: #6E322D;/*背景色*/
}
.p-mv_title-1st span::before,.p-mv_title-2nd span::before,.p-mv_title-3rd span::before{
  position: absolute;
  content: '';
  top: 100%;
  left: 0;
  border: none;
  border-bottom: solid 15px transparent;
  border-right: solid 20px rgb(149, 158, 155);/*折り返し部分*/

  }
.p-mv_title-1st span::after,.p-mv_title-2nd span::after,.p-mv_title-3rd span::after{
  position: absolute;
  content: '';
  top: 100%;
  right: 0;
  border: none;
  border-bottom: solid 15px transparent;
  border-left: solid 20px rgb(149, 158, 155);/*折り返し部分*/
}

